@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
/* Styling for the whole page. */

* {
  margin: 0;
  padding: 0;
}

html{
  font-size: 62.5%;
}

body, button{
  font-family: 'Roboto', sans-serif;
  font-size: 1.8rem;
  color: #fff;
}

body{
  width: 100vw;
  height: 100vh;
  background-image: url(../../static/media/bg.84aa7ab9.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-color: #0A090C;
  background-attachment: fixed;
}

#container{
  /*height: calc(100vh - 40px);*/
  /*padding: 20px;*/

  background-color: rgba(10,9,12,.25);
}

.flexWrapper{
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.fullHeight{
  min-height: calc(100vh - 40px);
}

.paddingAround{
  padding: 20px;
}

.home div p{
  font-size: 2.8rem;
  padding-bottom: 50px;
}

h1{
  text-transform: uppercase;
  text-align: right;
  font-size: 2.8rem;
  line-height: 1;
  font-weight: normal;

  color: #fff;

  margin-top: 50px;
}

h1 em{
  font-style: normal;
  font-size: 7.2rem;

  color: #C0DA74;
}

.smallTitle{
  font-size: 1.0rem;
}

.smallTitle em{
  font-size: 2.6rem;
}

button{
  background-color: #C0DA74;
  text-transform: uppercase;
  border: none;
  border-radius: 4px;
  height: 45px;
}

button:disabled{
  background-color: #CDD9A7;
}

.largeBtn{
  width: 100%;
  height: 45px;
}

.smallBtn{
  width: 85px;
}

.miniBtn{
  background-color: rgba(0,0,0,0);
  text-transform: none;
  color: #C0DA74;
  height: auto;
  margin-top: 50px;
  padding-top: 14px;
}

.closeBtn{
  background-color: rgba(0,0,0,0);
  text-transform: none;
  color: #C0DA74;
  height: auto;
  padding-left: 10px;
}

.wrapper{
  background-color: #BEBCB7;
  border-radius: 4px;
  text-align: center;
  flex-grow: 2;
  margin: 20px 0;
  padding: 50px 20px;
}

.form{
  flex-grow: 2;
}

h2{
  color: #0A090C;
  font-weight: normal;
  margin-bottom: 20px;
}

input{
  height: 45px;
  border: none;
  border-radius: 4px;
  margin-right: 10px;
  padding: 0 10px;
  font-size:
}

form{
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-bottom: 20px;
}

form input, form div{
  flex-grow: 2;
}

.empty{
  margin-top: 50px;
  font-size: 2.4rem;
  color: #706C61;
}

#ingredients{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

header{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.ingredient{
  background-color: #fff;
  color: #0A090C;
  padding: 10px 20px;
  border-radius: 25px;

  display: flex;
  flex-direction: row;
  margin: 5px;
}


h3{
  font-weight: normal;
  color: #C0DA74;
  text-transform: uppercase;
  font-size: 2.6rem;
}

.recipe{
  min-height: 200px;
  background-repeat: no-repeat;
  background-size: cover;
  background-color: #0A090C;

  margin: 10px 20px;
  border-radius: 4px;
}

.recipe div{
  min-height: 160px;
  background-color: rgba(10,9,12,.5);
  padding: 20px;

  border-radius: 4px;

  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.overlay{
  background-color: rgba(10,9,12,.50);
}

.recipeTitle{
  margin-top: 50px;
}

.instructions{
  background-color: #BEBCB7;
  color: #0A090C;
  flex-grow: 2;
  list-style-position: inside;
}

.instructions ul{
  padding-bottom: 50px;
}

.instructions ol li{
  padding-bottom: 10px;
}

.instructionFlex{
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.bgWrap{
  background-color: #BEBCB7;
  border-radius: 4px;
  margin: 20px 0;
  flex-grow: 2;
}

.loader{
  width: 50px;
  height: 50px;

  border: 16px solid #fff;
  border-top: 16px solid #C0DA74;
  border-radius: 50%;

  -webkit-animation: spin 1s linear infinite;

          animation: spin 1s linear infinite;
}

@-webkit-keyframes spin {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes spin {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

